import Input from "../../common/core/Input";
import React from "react";
import cn from 'classnames';
import styles from './style.module.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

export default () => (
  <Row>
    <Col className={'col-4'}>
      <Input field={'1'} placeholder={'Name'}/>
      <Input field={'2'} placeholder={'Surname'}/>
      <Input field={'3'} placeholder={'Date of birth'}/>
    </Col>
    <Col className={cn(styles.profileAddress, 'col-12')}>Address</Col>
    <Col className={'col-12'}>
      <Row>
        <Col className={'col-5 pr-1'}>
          <Row>
            <Col className={'col-7 pr-1'}>
              <Input field={'4'} placeholder={'Street'}/>
              <Input field={'5'} placeholder={'City'}/>
              <Input field={'6'} placeholder={'Country'}/>
            </Col>
            <Col className={'col-5 pl-1'}>
              <Input field={'7'} placeholder={'№'}/>
              <Input field={'8'} placeholder={'Index'}/>
              <Input field={'9'} placeholder={'State'}/>
            </Col>
          </Row>
        </Col>
        <Col className={'col-7 pl-1'}>
          <Row>
            <Col className={'col-6 pr-1'}>
              <Input field={'10'} placeholder={'Work email'}/>
              <Input field={'11'} placeholder={'Alternative email'}/>
              <Input field={'12'} placeholder={'Gender'}/>
            </Col>
            <Col className={'col-6 pl-1'}>
              <Input field={'13'} placeholder={'Facebook profile'}/>
              <Input field={'14'} placeholder={'Twitter profile'}/>
              <Input field={'15'} placeholder={'Linked-in profile'}/>
            </Col>
          </Row>
        </Col>
      </Row>
    </Col>
  </Row>
);