import React from "react";
import Col from "react-bootstrap/Col";
import cn from 'classnames';

import templatesIconPath from '../images/icons/menu/templates.png';
import documentsIconPath from '../images/icons/menu/documents.png';
import productsIconPath from '../images/icons/menu/products.png';
import servicesIconPath from '../images/icons/menu/services.png';
// import businessIconPath from '../images/icons/menu/business.png';
import marketIconPath from '../images/icons/menu/business.png';
import customersIconPath from '../images/icons/menu/customers.png';
import partnersIconPath from '../images/icons/menu/partners.png';
import competitorsIconPath from '../images/icons/menu/competitors.png';

import templatesActiveIconPath from '../images/icons/menu/active/templates.png';
import documentsActiveIconPath from '../images/icons/menu/active/documents.png';
import productsActiveIconPath from '../images/icons/menu/active/products.png';
import servicesActiveIconPath from '../images/icons/menu/active/services.png';
// import businessActiveIconPath from '../images/icons/menu/active/business.png';
import marketActiveIconPath from '../images/icons/menu/active/business.png';
import customersActiveIconPath from '../images/icons/menu/active/customers.png';
import partnersActiveIconPath from '../images/icons/menu/active/partners.png';
import competitorsActiveIconPath from '../images/icons/menu/active/competitors.png';

import settingsIconPath from '../images/icons/settings.png';

import Row from "react-bootstrap/Row";
import Image from "react-bootstrap/Image";

import styles from './style.module.css';
import {Link} from "react-router-dom";

const listIconPaths = [
  templatesIconPath,
  documentsIconPath,
  productsIconPath,
  servicesIconPath,
  // businessIconPath,
  marketIconPath,
  customersIconPath,
  partnersIconPath,
  competitorsIconPath,
];

const listActiveIconPaths = [
  templatesActiveIconPath,
  documentsActiveIconPath,
  productsActiveIconPath,
  servicesActiveIconPath,
  // businessActiveIconPath,
  marketActiveIconPath,
  customersActiveIconPath,
  partnersActiveIconPath,
  competitorsActiveIconPath,
];

const LOGOUT_PATH = '/login';

const AVATAR_SIZE = 50;
const USER_HEIGHT = 80;
const LIST_ITEM_HEIGHT = 45;
const PADDING_LEFT = 25;
const ICON_SIZE = 18;
const SETTINGS_ICON_SIZE = 17;

function Menu({user, items, width, activeTabIndex, setTabIndex, showUserInfo}) {
  return (
    <div className={styles.container} style={{width}}>
      <User user={user} contentWidth={width-PADDING_LEFT*2} showUserInfo={showUserInfo}/>
      <Row style={{minHeight: LIST_ITEM_HEIGHT*items.length}}>
        {items.map((item, index) => (
          <MenuListItem
            title={item.name}
            active={activeTabIndex === index}
            onClick={() => setTabIndex(index)}
            iconSrc={listIconPaths[index]}
            activeIconSrc={listActiveIconPaths[index]}
          />
        ))}
      </Row>
      <Row className={styles.copyrightContainer}>
        <div className={styles.copyrightText}>Copyright 2019. All rights Reserved</div>
      </Row>
    </div>
  );
}

const MenuListItem = ({iconSrc, activeIconSrc, title, active, onClick}) => (
  <Row
    onClick={onClick}
    className={cn(styles.listItemContainer, active && styles.active)}
    style={{paddingLeft: PADDING_LEFT, height: LIST_ITEM_HEIGHT}}
  >
    <Image hidden={active} width={ICON_SIZE} src={iconSrc}/>
    <Image hidden={!active} width={ICON_SIZE} src={activeIconSrc}/>
    <Col className={styles.titleContainer}>{title}</Col>
  </Row>
);

const User = ({user, contentWidth, showUserInfo}) => (
  <Row className={styles.userContainer} style={{height: USER_HEIGHT}}>

    <div className={styles.user} style={{width: contentWidth}}>
      <div
        className={styles.avatarContainer}
        style={{width: AVATAR_SIZE, height: AVATAR_SIZE}}
      >
        <Image
          className={styles.avatarImage}
          onClick={() => showUserInfo(true)}
          width={AVATAR_SIZE}
          src={user.avatarSrc}
        />
      </div>

      <Col className={styles.userInfoContainer}>
        <Row>
          <Col className={styles.userNameText}>{`${user.name} ${user.surname}`}</Col>
          <Col className={styles.settingsIconContainer}>
            <Image
              onClick={() => showUserInfo(true)}
              height={SETTINGS_ICON_SIZE}
              className={styles.settingsIcon}
              src={settingsIconPath}/>
          </Col>
        </Row>
        <Row className={styles.logoutContainer}>
          <Link to={LOGOUT_PATH}>Logout</Link>
        </Row>
      </Col>
    </div>

  </Row>
);

export default Menu;