import React from 'react';
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input from "../../common/core/Input";
import styles from '../style.module.css';
import {TextArea} from "informed";

// passing bool 'noHeader' to easy style it in UserSettings....
export default ({noHeader}) => (
  <Row>

    {!noHeader &&
    <Row className={styles.subheader + ' mb-3'}>
      Your company description and contacts
    </Row>}

    <Row>

      <Col className={"col-6 m-0 pr-2 text-center"}>

        <Input field={'1'} placeholder={'Company name'}/>
        <Input field={'2'} placeholder={'Date of establishment'}/>

        <Row>
          <Col className={"col-8 pr-2"}>
            <Input field={'3'} placeholder={'Street'}/>
            <Input field={'4'} placeholder={'City'}/>
            <Input field={'5'} placeholder={'Country'}/>
          </Col>
          <Col className={"col-4 pl-2"}>
            <Input field={'6'} placeholder={'№'}/>
            <Input field={'7'} placeholder={'Index'}/>
            <Input field={'8'} placeholder={'State'}/>
          </Col>
        </Row>

      </Col>

      <Col className={"col-6 m-0 pl-2 text-center"}>
        <Input field={'9'} placeholder={'Company registration number'}/>
        <Input field={'10'} placeholder={'Type of license, issued'}/>
        <Row>
          <Col className={"col-6 pr-2"}>
            <Input field={'11'} placeholder={'Certification'}/>
          </Col>
          <Col className={"col-6 pl-2"}>
            <Input field={'12'} placeholder={'Authorizations'}/>
          </Col>
        </Row>
        <Input field={'13'} placeholder={'Quality certificates, issued'}/>
        <Input field={'14'} placeholder={'Brand certificates and copyrights, issued'}/>
      </Col>

    </Row>

    <Col className={'col-12'}>
      <TextArea field={'15'} placeholder={'Banking details'} className={cn(styles.areaCompany, styles.banking)}/>
    </Col>
    <Col className={'col-12'}>
      <TextArea field={'16'} placeholder={'Text field max 3 propositions about founders and type of company'} className={styles.areaCompany}/>
    </Col>

  </Row>
);

