import React, {useState, useCallback} from 'react';
import styles from './style.module.css';
import {Form, TextArea, useFormApi} from 'informed';
import Button from '../../common/core/Button';
import ButtonsPair from '../../common/parts/ButtonsPair';
import Profile from './Profile';
import Company from '../../SignUp/contents/Company';
import Business from '../../SignUp/contents/Business';
import DashboardTabs from "../../common/parts/DashboardTabs";

//
const Assistant = () => null;

const TABS = [
  {
    name: 'PROFILE',
    content: <Profile/>,
    initialValues: {'1': 'Adam', '2': 'Smith'},
  },{
    name: 'COMPANY',
    content: <Company noHeader/>,
  },{
    name: 'BUSINESS',
    content: <Business grey noHeader/>,
    // initialValues: {'3': '2'},
  },{
    name: 'VIRTUAL ASSISTANT',
    content: <Assistant/>,
    // initialValues: {'3': '2'},
  },
];

////// TODO REMAKE need to save all data on click (ask)

export default function EditUserInfo({close}) {

  const onFormSubmit = (tabIndex, formState) => {
    // SEND DATA //
    // SAVE DATA TO CONTEXT //
    console.log(tabIndex, formState);
    close();
  };

  const forms = TABS.map((tab, index) => (
    <div className={styles.formContainer}>
      <Form
        className={styles.form}
        onSubmit={(formState) => onFormSubmit(index, formState)}
        initialValues={tab.initialValues}
      >
        {tab.content}
        <ButtonsPair onCancel={close}/>
      </Form>
    </div>
  ));

  return (
    <DashboardTabs
      tabs={TABS}
      startTabIndex={0}
      readyContents={forms}
    />
  );
}
