import moment from "moment";
import getSortedByDate from "./additional/getSortedByDate";

export const getProductsData = () => {
  const productItemShape = {
    code: 0,
    name: 'Lorem ipsum',
    price: '$120',
    measure: 'Lorem',
    producer: 'Lorem ipsum dolor',
    description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit',
    delivery: '5 days',
    keywords: 'lorem, ipsum, dolor',
  };

  return [...Array(100)]
    .fill(productItemShape)
    .map(({code, ...rest}, i) => ({code: i, ...rest}));
};

