export const getDocumentsData = () => {
  const itemShape = {
    id: 0,
    title: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit',
    htmlText: '',
    date: moment('02.02.2019').valueOf(),
    keywords: 'lorem, ipsum, dolor',
  };

  const documents = [...Array(25)]
    .fill(itemShape)
    .map(({id, ...rest}, i) => ({id: i, ...rest}));

  return getSortedByDate(documents);
};

